import React from 'react'
import Header from './component/Header'
import Footer from './component/Footer'

export default class App extends React.Component {
  render() {
    return (
      <div>
        <Header />
        <div className="page-content">
          <div className="wrapper">
            {this.props.children}
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}



