import React, { Component } from 'react'
import { Link } from 'react-router'

export default class PostList extends Component {
  render() {
    let postTitleStyle = {
      display: 'inline-block'
    };
    let commentCountStyle = {
      background: 'gray',
      borderRadius: '5px',
      color: 'white',
      padding: '2px 5px',
      margin: '0 10px',
      textDecoration: 'none'
    };
    let postItems = this.props.posts.map((post) => (
      <li key={post.id}>
        <span className="post-meta">{post.created_at}</span>
        <h2>
          <Link to={"/post/" + post.number} className="post-link" style={postTitleStyle}>{post.title}</Link>
          <span style={commentCountStyle}>{post.comments}</span>
        </h2>
      </li>
    ));
    return (
      <ul className="post-list">
        {postItems}
      </ul>
    )
  }
}



