import React from 'react'
import PostList from './PostList'
import Loading from './Loading'
import Api from '../Api.js'

export default class Tags extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isTagsLoading: true,
      isPostLoading: false,
      tags: [],
      posts: []
    };
    this.handleTagClick = this.handleTagClick.bind(this);
  }

  componentDidMount() {
    Api.getTagList().then(tags => {
      if (!this.isUnmounted) {
        this.setState({
          isTagsLoading: false,
          tags: tags
        });
      }
    });
  }

  handleTagClick(e) {
    e.preventDefault();

    this.setState({
      isPostLoading: true,
      posts: []
    });

    let tagName = e.target.innerText;
    Api.getPostListWithTag(tagName).then(posts => {
      this.setState({
        isPostLoading: false,
        posts: posts
      });
    });
  }

  render() {
    let tagItems = this.state.tags.map((tag) => (
      <li key={tag.id}>
        <a href='#' onClick={this.handleTagClick}>{tag.name}</a>
      </li>
    ));
    return (
      <div className="home">
        <h1 className="page-heading"></h1>
        <Loading visible={this.state.isTagsLoading} />
        <ul>
          {tagItems}
        </ul>
        <h1 className="page-heading"></h1>
        <Loading visible={this.state.isPostLoading} />
        <PostList posts={this.state.posts} />
      </div>
    )
  }
}



