import React from 'react'
import PostList from './PostList'
import Loading from './Loading'
import Api from '../Api'

export default class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      posts: []
    };
  }

  componentDidMount() {
    Api.getPostList().then(posts => {
      this.setState({
        isLoading: false,
        posts: posts
      });
    });
  }

  render() {
    return (
      <div className="home">
        <h1 className="page-heading"></h1>
        <Loading visible={this.state.isLoading} />
        <PostList posts={this.state.posts} />
        {/*<p className="rss-subscribe"><a href="/feed.xml">RSS</a></p>*/}
      </div>
    )
  }
}



