import React from 'react'

export default class Loading extends React.Component {
  render() {
    let style = {
      display: this.props.visible ? 'block' : 'none',
      textAlign: 'center'
    };
    return (
      <div style={style}>
        <img src="/loading.gif" alt="Loading..." />
      </div>
    )
  }
}



