import config from './config.js'

const headers = new Headers();

const init = {
  method: 'GET',
  headers: headers
};

export default {

  // 
  // GET /repos/:owner/:repo/labels
  getTagList() {
    let url = `https://api.github.com/repos/${config.user}/${config.repo}/labels`;
    return fetch(url, init).then(resp => {
      if (resp.status === 200) {
        return resp.json();
      }
      throw new Error('GithubApiError');
    }).then(tags => tags.filter(tag => !tag.default));
  },

  // 
  getPostList() {
    let url = `https://api.github.com/repos/${config.user}/${config.repo}/issues`;
    return fetch(url, init).then(resp => {
      if (resp.status === 200) {
        return resp.json();
      }
      throw new Error('GithubApiError');
    });
  },

  // 
  getPostListWithTag(label) {
    let url = `https://api.github.com/repos/${config.user}/${config.repo}/issues?labels=${label}`;
    return fetch(url, init).then(resp => {
      if (resp.status === 200) {
        return resp.json();
      }
      throw new Error('GithubApiError');
    });
  },

  // 
  getPost(postNumber) {
    let url = `https://api.github.com/repos/${config.user}/${config.repo}/issues/${postNumber}`;
    return fetch(url, init).then(resp => {
      if (resp.status === 200) {
        return resp.json();
      }
      throw new Error('GithubApiError');
    });
  },

  // 
  getCommentList(postNumber) {
    let url = `https://api.github.com/repos/${config.user}/${config.repo}/issues/${postNumber}/comments`;
    return fetch(url, init).then(resp => {
      if (resp.status === 200) {
        return resp.json();
      }
      throw new Error('GithubApiError');
    });
  },
}



